.card {
  width: 350px;
  margin: 15px auto;
  box-shadow: 5px 5px 10px 1px rgba(0, 0, 0, 0.5);
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 2s ease;
}
.card:hover {
  position: relative;
  bottom: 10px;
  top: 10px;
  color: #fff;
  background: #000;
}
img {
  width: 100px;
  margin-right: 10px;
  padding: 10px;
}
